.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 10%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    border-radius: 5px;
    overflow-y: scroll;
    height: auto;
    max-height: 80vh;
}

@media (min-width: 500px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}

@media (min-width: 1000px) {
    .Modal {
        width: 600px;
        left: calc(50% - 250px);
    }
}
